import * as React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import Image from 'gatsby-image';
import SocialProfile from '../../components/social-profile/social-profile';
import {
  IoLogoLinkedin,
  IoLogoGoogle,
} from 'react-icons/io';
import {
  AboutWrapper,
  AboutImage,
  AboutPageTitle,
  AboutDetails,
  SocialProfiles,
} from './style';
import styled from 'styled-components';
import { layout } from 'styled-system';

const SocialLinks = [
  {
    icon: <IoLogoLinkedin />,
    url: 'https://www.linkedin.com/in/savelle/',
    tooltip: 'LinkedIn',
  },
  {
    icon: <IoLogoGoogle />,
    url: 'mailto:savellem@gmail.com',
    tooltip: 'Email',
  },
];

const Box = styled.div`
  ${layout}
`;

interface AboutProps {}

const About: React.FunctionComponent<AboutProps> = () => {
  const Data = useStaticQuery(graphql`
    query {
      avatar: file(absolutePath: { regex: "/about.jpg/" }) {
        childImageSharp {
          fluid(maxWidth: 1770, quality: 90) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      site {
        siteMetadata {
          author
          about
        }
      }
    }
  `);

  return (
    <AboutWrapper>
      <AboutPageTitle>
        <h2>About</h2>
        <p>
          My name is Savelle, I have 14 years of experience building web and mobile applications. I grew up in New Mexico, where every thing is made from scratch. I take that mentality, and treat every project with the same attention and care. It helps to understand both aesthetic and function equally. In my case, that involves a life long mastering of both design and development. 
        </p>
      </AboutPageTitle>

      <AboutImage>
        <Image fluid={Data.avatar.childImageSharp.fluid} alt="author" />
      </AboutImage>

      <AboutDetails>
                <h2>Experience</h2>
                <h4>PLEXUS WORLDWIDE / 2016 - CURRENT</h4>
                <p>As the first digital designer hired to the marketing team, I lead 3 consecutive redesigns of plexusworldwide.com. This includes a custom ecommerce store, ambassador replicated site builder, back office dashboard and marketing landing pages. Currently, our team is developing an atomic design system in both Sketch and Angular components based on feedback from A/B user testing, cognitive walkthroughs and data analysis. I have also designed and developed landing pages, contest web applications, auto responder email templating systems, ZenDesk and Sharepoint theming, Custom Wordpress themes and plugins.</p>

                <h4>PETSMART / 2015 - 2016</h4>
                <p>Our team redesigned petsmart.com’s ecommerce experience based on A/B user testing, data analysis and cognitive walkthrough. I maintained the homepage updates, including collaboration with Universal Studios for the Secret Life of Pets movie promotions and campaigns.</p>

                <h4>HOMESMART LLC / 2015</h4>
                <p>Working closely with the IT department, I helped design the dashboard experience for agent real estate search tools. I also built landing pages, email campaigns, social networking graphics and developed the blog.</p>

                <h4>SIMPLE GROUP INC / 2014-2015</h4>
                <p>I designed and developed custom websites for clients including Mike Love, Sarkissian Mason and Bare Minerals. I also provided brand development, strategy and interfaced with clients.</p>

                <h4>MYPHONEROOM LLC / 2013-2015</h4>
                <p>As Freelance Contractor, I worked both in-house and remotely designing local and small business website projects.</p>

                <h4>XHIBIT CORP / 2012-2013</h4>
                <p>I lead the visual design efforts for beachboys.com, General Motors Chevy Silverado 2014 vehicle customizer, Medaire iPad application, Rich Dad Poor Dad landing pages and video teaser.</p>

                <h4>ZION &amp; ZION / 2006-2011</h4>
                <p>I designed and developed digital exhibits for Arizona Science Center, zionandzion.com redesign, Sotheby’s brochures, Premier Pools TV commercial, Imperial Wholesale custom kitchen tile touchscreen application and various landing pages for small to mid-sized clients.</p>
                <hr/>
                <h2>Skills</h2>

                <h4>Design</h4>
                <p>Illustration &amp; UI graphics, Strategy &amp; vision presentations, User Flows, Concept Sketches, Wireframes &amp; mock ups with Sketch, Style guides &amp; pattern library, Atomic design systems</p>

                <h4>Prototyping</h4>
                <p>Rapid prototyping using Invision, Interactive flow with HTML/CSS/JS, Frameworks(Angular, Metalsmith, Wordpress) &amp; APIs</p>

                <h4>Research</h4>
                <p>A/B Testing &amp; Experiment, Cognitive walkthrough, Data Analysis</p>

                <h4>Collaboration</h4>
                <p>Facilitating design critique, Stakeholder presentations, Self Starter, Detail oriented, Communicative, Mentor, Agile Sprints (Collaboration with product managers, developers, researchers &amp; creative department)</p>

                <hr/>

                <h2>Education</h2>
                <h4>COLLINS COLLEGE / 2003-2006</h4>
                <p>I received a Bachelor's of Arts degree in Game Design at Collin's College. I was introduced to all aspects of interactive design. The curriculum included Design, Programming, Creative Writing, Video and Music Production.</p>

        <SocialProfiles>
          <SocialProfile items={SocialLinks} />
        </SocialProfiles>
      </AboutDetails>
    </AboutWrapper>
  );
};

export default About;
